___CSS_LOADER_EXPORT___.push([module.id, `/* Overall font styling */
.service-page {
    position: relative;
    font-family: "Helvetica", sans-serif;
  }
  
  /* --- Hero Section --- */
  .service-hero {
    position: relative;
    width: 100%;
    height: 90vh;
    background-size: cover;
    background-position: center;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    color: #fff;
  }
  
  .services-hero-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.4);
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    text-align: center;
    padding-top: 2rem;
  }
  
  .services-hero-content-title {
    display: flex;
    flex-direction: row;
    align-items: baseline;
  }
  
  .company-name-services {
    font-size: 0.7rem !important;
    font-weight: lighter;
    letter-spacing: 1.5px;
    margin: 0 12px;
    padding: 40px 20px;
  }
  
  .line {
    width: 30px;
    height: 1px;
    background-color: rgba(255, 255, 255, 0.5);
    margin: 20px;
  }
  
  .services-hero-overlay h1 {
    font-size: 2.6rem;
    font-weight: 700;
    margin-bottom: 1rem;
    text-shadow: 2px 2px 4px rgba(119, 116, 148, 0.6);
    width: 70%;
    margin: 40px auto;
  }
  
  .services-hero-overlay p {
    font-size: 1.1rem;
    line-height: 1.4;
    max-width: 600px;
    margin: 0 auto 1.5rem auto;
  }
  
  /* CTA Button */
  .cta-button {
    display: inline-block;
    padding: 0.75rem 1.5rem;
    background-color: #00a38c;
    color: #fff;
    border-radius: 5px;
    text-decoration: none;
    font-weight: 600;
    transition: background-color 0.3s;
  }
  .cta-button:hover {
    background-color: #008977;
  }
  
  /* --- Overlay Images bridging hero & next content --- */
  .overlay-images {
    position: absolute;
    top: 100vh;
    left: 50%;
    transform: translateX(-50%);
    z-index: 5;
    width: 300px;
    height: 200px;
  }
  
  .overlay-images .image1 {
    position: absolute;
    top: 0;
    left: 0;
    width: 70%;
    border-radius: 6px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
  }
  
  .overlay-images .image2 {
    position: absolute;
    top: 18vh;
    left: 35%;
    width: 70%;
    border-radius: 6px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
  }
  
  /* --- Main Content Section --- */
  .service-content {
    max-width: 1200px;
    margin: 0rem auto;
    padding: 0 1rem;
  }
  
  .content-section {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2rem;
  }
  
  /* If you want side-by-side on large screens, uncomment:
  @media (min-width: 768px) {
    .content-section {
      flex-direction: row;
      align-items: flex-start;
    }
  }
  */
  /* Container for the provider text and image */
.provider-container {
  display: flex;
  flex-direction: row;
  gap: 1rem;
  margin-top: 1rem;
  align-items: center;
}

/* Text block */
.provider-text {
  flex: 1;
}

/* Image block */
.provider-image img {
  width: 100%;
  max-width: 80px; /* Adjust as needed */
  border-radius: 4px;
  display: block;
}


  /* Fading bottom of content image */
  .content-image {
    width: 100%;
    min-width: 100vw;
    border-radius: 8px;
    -webkit-mask-image: linear-gradient(
      to bottom,
      rgba(0, 0, 0, 1) 80%,
      rgba(0, 0, 0, 0) 100%
    );
    mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1) 80%, rgba(0, 0, 0, 0) 100%);
    -webkit-mask-repeat: no-repeat;
    mask-repeat: no-repeat;
  }
  
  #services-title-small {
    margin: 4rem 0rem 0rem 0rem;
    text-align: left;
    font-size: 2.5rem;
  }
  
  .content-text {
    flex: 1;
    font-size: 1rem;
    line-height: 1.6;
    color: #333;
  }
  .content-text p {
    margin-bottom: 1rem;
  }
  
  /* --- INFO SECTION with background image --- */
  .info-section {
    max-width: 1200px;
    margin: 2rem auto 0 auto;
    padding: 2rem;
    background-size: cover;
    background-position: center;
    border-radius: 8px;
    position: relative;
  }
  
  /* Dark overlay for better text contrast */
  .info-overlay {
    background-color: rgba(0, 0, 0, 0.7); /* Increase alpha to 0.5 or 0.6 */
    padding: 2rem;
    border-radius: 8px;
    color: #fff;
  }
  
  .info-title {
    font-size: 1.8rem;
    margin-bottom: 1rem;
    color: #fff;
  }
  
  .info-subtitle {
    font-size: 1.4rem;
    margin-top: 1.5rem;
    margin-bottom: 0.75rem;
    color: #fff;
  }
  
  .info-text {
    font-size: 1rem;
    line-height: 1.6;
    color: #fff;
    margin-bottom: 1rem;
  }
  
  .info-list {
    list-style: none;
    padding-left: 0;
    margin: 1rem 0;
  }
  
  .info-list li {
    position: relative;
    padding-left: 2rem;
    margin-bottom: 0.75rem;
  }
  
  .info-list li::before {
    content: "✓";
    position: absolute;
    left: 0;
    top: 0;
    color: #00a38c;
    font-weight: bold;
  }

  /* Example: adding a subtle shadow to text in the .info-overlay */
.info-overlay h2,
.info-overlay h3,
.info-overlay p,
.info-overlay li {
  text-shadow: 1px 1px 3px rgba(0,0,0,0.7);
}

